.wp-block-phonicscore-githubauthvideo {
  background-color: var(--wp-admin-theme-color);
  color: #fff;
  padding: 2px; }

.video-auth-spash {
  background-repeat: no-repeat;
  background-size: contain;
  min-height: 330px;
  text-align: center; }

.video-auth-splash-cover {
  width: 100%;
  height: 100%;
  min-height: 330px;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.sponsor-message-block {
  color: #ff0000;
  margin-bottom: 50px; }

.github-icon {
  display: inline;
  margin-right: 5px;
  vertical-align: middle; }

.github-button-text {
  vertical-align: middle; }

.video-js-container {
  min-height: 75%; }

.video-js {
  height: 100%; }
